<template>
  <div class="checkbox-container">
    <input
      :checked="checked"
      :class="'task-checkbox' + (disabled ? '' : ' enabled-checkbox')"
      type="checkbox"
      :title="'Mark task ' + (checked ? 'in' : '') + 'complete'"
      :disabled="disabled"
      @change="completeTask({id: taskId})"
    >
    <span class="check-custom" />
  </div>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
  name: 'Checkbox',
  
  props: {
    checked: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    taskId: {
      type: Number,
      default: null
    }
  },
  
  methods: {
    ...mapMutations([
      'completeTask'
    ])
  }
}
</script>

<style scoped lang="scss">
/* Adapted from https://hackernoon.com/hacking-custom-checkboxes-and-radios-5d48230440d */

$checkbox-size: 2.2rem;

.checkbox-container {
  position: relative;
  min-width: $checkbox-size;
  width: $checkbox-size;
  height: $checkbox-size;
  margin-right: 20px;
}

.checkbox-container > * {
  position: absolute;
  left: 0;
  width: $checkbox-size;
  height: $checkbox-size;
}

/* Styles for hiding the native checkbox */
.task-checkbox {
  z-index: 2;
  opacity: 0;
  cursor: pointer;
}

/* Styles for the basic appearance of the custom checkbox */
.check-custom {
  border: 2px solid #969696;
  border-radius: 50%;
}

/* Styles for the hover state of the custom checkbox */
.enabled-checkbox:hover ~ .check-custom {
  border-color: #b0d5ff;
  box-shadow: 0 0 0 2px rgba(23, 133, 255, 0.25);
}

/* Styles for the checked state of the custom checkbox */
.task-checkbox:checked ~ .check-custom {
  border-color: #1785ff;
  background: #1785ff url(data:image/svg+xml;base64,) center no-repeat;
  background-size: 75%;
}
</style>
